<template>
  <div class="clearfix h100 productdetails">
    <div class="productdetailsleft w50 ">
      <Swipermodule :pic="pruductdetails.ring_pic"></Swipermodule>
    </div>
    <div class="productdetailsright w50">
      <div class="head">
        <div class="title">
          <div>
            <h4>{{pruductdetails.name}}</h4>
            <span>{{pruductdetails.fnum}}</span>
          </div>
          <p><span class="fc-gray ls2"></span><span class="fc-blue f36" style="margin-left: -.3rem;"><span class="fc-blue"></span>
            <span class="fc-blue f36" v-if="!countpricedata">{{pruductdetails.p_min}} ~ {{pruductdetails.p_max}}</span>
            <span class="fc-blue f36" v-if="countpricedata">{{countpricetxt}}</span>
          </span>
            <span v-if="collectData == '0' " @click="collectPort()"><img
              src="../../../assets/image/icon/disfavorite.png" alt=""> </span>
            <span @click="clearcollect" v-if="collectData == '1' " class="fc-gray"><img src="../../../assets/image/icon/favorite.png" alt=""> </span>
          </p>
          <h5 class="fblack"></h5>
        </div>
        <div class="details">
          <ul class="clearfix">
            <li class="pr"><span class="fc-gray"></span>
              <!-- <span v-if="pruductdetails.shape=='ROUND'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='PRINCESS'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='EMERALD'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='HEART'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='OVAL'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='PEAR'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='MARQUISE'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='ASSCHER'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='CUSHION'" class="fc-gray"></span>
              <span v-if="pruductdetails.shape=='TRILLION'" class="fc-gray"></span> -->
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
              <span v-if="pruductdetails.shape==''" class="fc-gray"></span>
            </li>
            <li class="pr"><span class="fc-gray ls3"></span> <span class="fc-gray ml1">{{pruductdetails.w_min}} - {{pruductdetails.w_max}} ct</span>
            </li>
            <li class="pr">
              <span class="fc-gray ls2"></span>
              <span class="fc-gray ml">{{pruductdetails.j_min}} - {{pruductdetails.j_max}} </span>
            </li>
            <li class="pr"><span class="fc-gray ls3"></span> <span class="fc-gray ml1">{{pruductdetails.f_min}} - {{pruductdetails.f_max}} ct</span>
            </li>
          </ul>
        </div>
      </div>
      <div class="foot ">
        <form class="bt1" action="javascript:;">
          <ul>
            <li>
              <span class="fc-gray ls2"></span>
              <span class="ml3 w80" style="display:inline-block">
                <el-radio-group v-model="texturedata" size="small" class="linecheckbox">
                  <el-radio-button v-for="(item,index) in texturelist" :key="index" :label="item.name"></el-radio-button>
                  <span class="fc-red" v-show="tipsisshow"></span>
                </el-radio-group>
            </span>
              
            </li>
            <li><span class="fc-gray ls2"></span>
              <el-select  class="ml3" clearable  v-model="HandNumselected" size="small" placeholder="">
                <el-option value="">
                </el-option>
                <el-option
                  v-for="item in HandNum"
                  :key="item"
                  :label="item"
                  :value="item"
                >
                </el-option>
              </el-select>
              <span class="fc-red ml-1" v-show="tipsisshow"></span>
            </li>
            <li v-if="set.gem == '1'">
              <span style="vertical-align: top;" class="fc-gray ls2"></span>
              <el-input step="0.01" type="number" style="width: 2rem"  class="ml3 w20"  placeholder="" v-model="StoneNumselected">
                <template slot="append">ct</template>
              </el-input>
              <span class="fc-red ml-1" v-show="tipsisshow"></span>
            </li>
            <li>
              <span class="fc-gray vertical-align-top ls2"></span>
              <el-input
                class="ml3 form-controle f26"
                type="textarea"
                autosize
                placeholder=""
                v-model="letteringtext">
              </el-input>
            </li>
          </ul>
          <div class="btn-box">
            <button v-if="set.ring == '1'" class="btn-w" @click="buyrings()"></button>
            <button @click="skippagekc()" v-if="set.ring == '1'" class="btn-b"></button>
            <!---->
            <button @click="skippagelz()"  v-if="set.ring == '3'" class="btn-b"></button>
            <!---->
            <button v-if="set.ring == '2'" @click="skippage()"  class="btn-b"></button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
  export default {

    created() {
      this.initialize();
      this.restset();
      if(this.$route.query.did){
        this.did=this.$route.query.did
      };
      if(this.$route.query.num){
        this.rid=this.$route.query.num
      };
    },
    data() {
      return {
        /**/
        set: JSON.parse(this.$route.query.set),
        /**/
        did: this.$route.query.did,
        rid: this.$route.query.num,
        /**/
        texturelist: '',
        texturelistone: '',
        texturedata: this.texturelistone,
        /**/
        HandNumselected: '',
        /*,*/
        tipsisshow: false,
        /**/
        letteringtext: '',
        HandNum: '',
        /**/
        StoneNumselected: '',
        /**/
        pruductdetails: '',
        /**/
        collectData: '0', /*01*/
        /**/
        countpricedata: false,
        countpricetxt: '',
        /**/
        clientmsg: {
          lettering:'',
          StoneNumselected:'',
          material:'',
          size:'',
        },
        /**/
        pricepushdata: '',
        /**/
        choosediadata:{
          maxweight:'',
          minweight:'',
          shape:''
        }
      }
    },
    methods: {
      /**/
      clearcollect(){
        var url = this.BASEURL+'/index.php?action=clean_collection';
        this.$http.get(url, {
          params: {
            id: this.rid,
          }
        }, {}).then(response => {
          if(response.body.error_code == 0){
            if(response.body=='1'){
              this.collectData = '0'
            }else if(response.body=='0'){
              this.collectData = '1'
            }
          }
        }, response => {
          console.log(response);
        });
      },
      /**/
      initialize() {
        var url = this.BASEURL+'/index.php?action=ringcare_detail';
        this.$http.get(url, {
          params: {
            rid: this.rid,
          }
        }, {}).then(response => {
          if(response.body.error_code == 0) {
            this.pruductdetails = response.body.result;
            this.HandNum = response.body.result.handsize;
            this.collectData = response.body.result.collection;
            this.texturelist = response.body.result.material;
            this.choosediadata.minweight = this.pruductdetails.w_min;
            this.choosediadata.maxweight = this.pruductdetails.w_max;
            this.choosediadata.shape = this.pruductdetails.shape;
          }
        }, response => {
          console.log(response);
        });
      },
      /**/
      buyrings() {
        /**/
        if (this.HandNumselected != '0' && this.StoneNumselected != '') {
          if (this.StoneNumselected >this.pruductdetails.w_max ||  this.StoneNumselected < this.pruductdetails.w_min) {
            this.messagedata = '';
            this.message();
            this.tipsisshow = true;
          }else{
            this.tipsisshow = false;
            this.openonlydiamond('','','','Shopcar','Productlist',true)
          }
        } else {
          this.tipsisshow = true;
        }
      },
      /**/
      collectPort() {

        var url = this.BASEURL+'/index.php?action=collection';
        this.$http.get(url, {
          params: {
            rid: this.rid,
          }
        }, {}).then(response => {
          if (response.body.result == 'SUCCESS') {
            this.collectData = '1'
          }
        }, response => {
          console.log(response);
        });
      },
      /**/
      countprice() {
        if(this.texturedata != '' && this.HandNumselected != ''){
          if(this.zhisshow){
            if (this.texturedata && this.HandNumselected!='0' && this.StoneNumselected && this.StoneNumselected!='0') {
              this.countpricefun();
              this.countpricedata = true;
            } else {
              this.countpricedata = false;
            }
          }else {
            if (this.texturedata && this.HandNumselected!='0') {
              this.countpricefun();
              this.countpricedata = true;
            } else {
              this.countpricedata = false;
            }
          }
        }else {
          this.countpricedata = false;
        }
      },
      /**/
      countpricefun(){
        var url = this.BASEURL+'/index.php?action=ring_price';
        this.$http.get(url, {
          params: {
            rid: this.rid,
            size: this.HandNumselected,
            material: this.texturedata,
          }
        }, {}).then(response => {
          if(response.body.error_code == 0){
            this.countpricedata = true;
            this.countpricetxt = response.body.result.total_price;
            this.clientmsg.size = this.HandNumselected;
            this.clientmsg.material = this.texturedata;
            this.clientmsg.StoneNumselected = this.StoneNumselected;
            this.clientmsg.lettering = this.letteringtext;
            this.pricepushdata = response.body.result;
          }
        }, response => {
          console.log(response);
        });
      },
      /**/
      skippage(){
        /**/
        /**/
        if(this.zhisshow) {
          if (this.texturedata && this.HandNumselected!='0' && this.StoneNumselected && this.StoneNumselected!='0') {
            this.tipsisshow = false;
            this.countprice();
            /**/
            this.$router.push({
              name: 'CommoncustomOrder',
              query: {clientmsg:  JSON.stringify(this.clientmsg), pricepushdata: JSON.stringify(this.pricepushdata), rid: this.rid,did: this.did}
            });
          } else {
            this.tipsisshow = true;
          }
        }else {
          /**/
          if (this.texturedata && this.HandNumselected!='0' ) {
            this.tipsisshow = false;
            this.countprice();
            /**/
            this.$router.push({
              name: 'CommoncustomOrder',
              query: {clientmsg:  JSON.stringify(this.clientmsg), pricepushdata: JSON.stringify(this.pricepushdata), rid: this.rid,did: this.did,letteringtext:this.letteringtext}
            });
          } else {
            this.tipsisshow = true;
          }
        }

      },
      /*,*/
      skippagekc(){

        if (this.texturedata && this.HandNumselected!='') {
          this.tipsisshow = false;
          this.countprice();
          /**/
          this.$router.push({
            name: 'InventoryList',
            query: {set:JSON.stringify(this.set),clientmsg: JSON.stringify(this.clientmsg), pricepushdata: JSON.stringify(this.pricepushdata), rid: this.rid,did: this.did,letteringtext:this.letteringtext,choosediadata:JSON.stringify(this.choosediadata)}
          });
        } else {
          this.tipsisshow = true;
        }
      },
      skippagelz(){
        if (this.texturedata && this.HandNumselected!='0' ) {
          this.tipsisshow = false;
          this.countprice();
          /**/
          this.$router.push({
            name: 'InventoryList',
            query: {set:JSON.stringify(this.set),clientmsg:  JSON.stringify(this.clientmsg), pricepushdata: JSON.stringify(this.pricepushdata), rid: this.rid,letteringtext:this.letteringtext,choosediadata:JSON.stringify(this.choosediadata)}
          });
        } else {
          this.tipsisshow = true;
        }
      },
    },
    watch: {
      // 
      $route(){
        this.initialize();
        this.restset();
        if(this.$route.query.did){
          this.did=this.$route.query.did
        };
        if(this.$route.query.num){
          this.rid=this.$route.query.num
        };
      },
      texturedata: function () {
        this.countprice();
      },
      HandNumselected: function () {
        this.countprice();
      },
      StoneNumselected: function (val) {
        if((/([1-9]\d*(\.\d*[1-9])?)|(0\.\d*[1-9])/).test(val)){
          this.countprice();
        }else {
          return false
        }

      },
    }
  }

</script>
<style type="text/css">


</style>



